<template>
	<view class="wrapper">
		<view class="header">
			<view class="btn menu">
				<image src="/static/images/menu.png" mode="heightFix"></image>
			</view>
			<view class="title">
				Image Designer
			</view>
			<view class="btn edit">
				<image src="/static/images/write.png" mode="heightFix"></image>
			</view>
		</view>

		<view class="content">
			<view class="promt">
				<view class="input">
					<textarea cols="30" rows="10" placeholder="输入提示词" v-model="promt"></textarea>
				</view>
				<view class="bottom">
					<view class="btn" @click="createImage()" v-if="!loading">
						<span>创建</span>
					</view>
					<view class="btn" style="background-color: #888888;" v-else>
						创建中···
					</view>
					<view class="cishu">
						<image src="/static/images/3JQv1AA40nZbahgNzi-GyHxBjsY.svg" mode="widthFix"
							style="width: 24px;margin-right: 6px;"></image>
						<span>78</span>
					</view>
				</view>

			</view>
			<view class="result">
				<view class="title">
					创建结果
				</view>
				<view class="imageWrap" v-if="imageUrl">
					<image @load="showImage($event)" :src="imageUrl" mode="widthFix" @click="clickImg(imageUrl)"></image>
				</view>
				<view class="imageWrap" v-else >
					nothing···
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	const App = getApp();
	export default {
		data() {
			return {
				baseUrl: 'https://www.scdpf.xyz',
				email: '',
				promt: '',
				loading: false,
				imageUrl: ''

			};
		},
		onShow() {
			this.setTabBarIndex(2);
			this.email = App.globalData.unionID;
		},
		methods: {
			gotoWeb() {
				uni.reLaunch({
					url: '/pages/subpkg/pay/pay'
				})
			},
			clickImg(url) {
				uni.previewImage({
					urls: [url], //需要预览的图片http链接列表，多张的时候，url直接写在后面就行了
					// current: '', // 当前显示图片的http链接，默认是第一个
					success: function(res) {},
					fail: function(res) {},
					complete: function(res) {},
				})
			},

			showImage(e) {
				uni.hideLoading()
				console.log('图片信息', e)
			},
			createImage() {
				const that = this;
				that.loading = true;
				uni.request({
					url: that.baseUrl + '/create_image',
					method: 'POST',
					data: {
						text: that.promt,
						num: 1,
						size: "1024x1024"
					},
					success(res) {
						if(res.data.code == 200) {
							console.log(res.data.image_url);
							that.imageUrl = res.data.image_url;
							that.loading = false;
							uni.showLoading({
								title: '加载中...',
								mask: true
							})
						} else {
							uni.showToast({
								title: '错误',
								icon: 'error',
								duration: 3000, //单位毫秒
								success(res) {
									that.loading = false
								}
							});
						}
						
					}
				})
			}
		},
	};
</script>

<style lang="scss">
	.wrapper {
		.header {
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-bottom: 1px solid #D9D9D9;

			.btn {
				image {
					width: 18px;
					height: 18px;
					padding: 13px 12px;
				}
			}
		}

		.content {
			width: 100%;
			box-sizing: border-box;
			height: calc(100vh - 100px);
			background-color: #f5f5f5;

			.promt {
				width: 100%;
				box-sizing: border-box;
				padding: 8px;

				.input {
					width: 100%;
					border-radius: 12px;
					box-shadow: 0 0 0 1px rgba(0, 0, 0, .05), 0 2px 4px 1px rgba(0, 0, 0, .09);
					background-color: #ffffff;

					textarea {
						box-sizing: border-box;
						padding: 20px;

					}
				}

				.bottom {
					margin-top: 8px;
					width: 100%;
					box-sizing: border-box;
					display: flex;
					align-items: center;
					justify-content: space-between;

					.btn {
						width: 100%;
						height: 40px;
						border-radius: 24px;
						background-color: #4E6AEB;
						display: flex;
						align-items: center;
						justify-content: center;
						font-size: 14px;
						color: #ffffff;
					}

					.cishu {
						padding-left: 10px;
						box-sizing: border-box;
						display: flex;
						align-items: center;
						font-size: 14px;
						color: #000000;
					}
				}
			}

			.result {
				padding: 8px;
				padding-top: 24px;
				box-sizing: border-box;

				.title {
					margin-left: 6px;
					font-size: 14px;
					color: #000000;
					margin-bottom: 24px;
				}

				.imageWrap {
					padding: 12px 10px;
					box-sizing: border-box;
					width: 100%;
					display: flex;
					align-items: center;
					justify-content: center;

					image {
						width: 100%;
						height: auto;
					}
				}
			}
		}
	}
</style>